@font-face {
    font-family: 'pretendard';
    font-style: normal;
    src: url('./font/Pretendard-Light.otf') format('opentype');
  }
  
  @font-face {
    font-family: 'pretendard-bold';
    font-style: normal;
    src: url('./font/Pretendard-Bold.otf') format('opentype');
  }
  
  @font-face {
    font-family: 'Inter';
    font-style: normal;
    src: url('./font/ArchivoNarrow-Regular.ttf') format('truetype');
  }
  
  @font-face {
    font-family: 'Hahmlet', serif;
    font-style: normal;
    src: url('./font/Hahmlet-Medium.ttf') format('truetype');
  }

.g-title {
    position: absolute;
    width: 854px;
    height: 70px;
    left: 293px;
    top: 165px;

    font-family: 'Hahmlet';
    font-style: normal;
    font-weight: 400;
    font-size: 70px;
    line-height: 70px;
    /* identical to box height */

    text-align: center;
    letter-spacing: -0.05em;

    color: #5590C9;
}

.g-subtitle {
    position: absolute;
    width: 404px;
    height: 30px;
    left: 533px;
    top: 260px;

    font-family: 'Pretendard';
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 30px;
    /* identical to box height */

    text-align: center;
    letter-spacing: -0.03em;

    color: #5590C9;

}