@font-face {
  font-family: 'pretendard';
  font-style: normal;
  src: url('./font/Pretendard-Light.otf') format('opentype');
}

@font-face {
  font-family: 'pretendard-bold';
  font-style: normal;
  src: url('./font/Pretendard-Bold.otf') format('opentype');
}

@font-face {
  font-family: 'Inter';
  font-style: normal;
  src: url('./font/ArchivoNarrow-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Hahmlet', serif;
  font-style: normal;
  src: url('./font/Hahmlet-Medium.ttf') format('truetype');
}

.App {
  position: relative;
  width: 1440px;
  height: 1024px;
  background: #F5F5F5;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
