@font-face {
    font-family: 'pretendard';
    font-style: normal;
    src: url('./font/Pretendard-Light.otf') format('opentype');
  }
  
  @font-face {
    font-family: 'pretendard-bold';
    font-style: normal;
    src: url('./font/Pretendard-Bold.otf') format('opentype');
  }
  
  @font-face {
    font-family: 'Inter';
    font-style: normal;
    src: url('./font/ArchivoNarrow-Regular.ttf') format('truetype');
  }
  
  @font-face {
    font-family: 'Hahmlet', serif;
    font-style: normal;
    src: url('./font/Hahmlet-Medium.ttf') format('truetype');
  }

  .Modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
  }
   
  /* modal창 */
  .modalBody {
    position: absolute;
    width: 700px;
    height: 610px;
    padding: 40px;
    text-align: center;
    background: #F5F5F5;
    border-radius: 62px;
    box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
  }
   
  #modalCloseBtn {
    position: absolute;
    top: 15px;
    right: 15px;
    border: none;
    color: rgba(0, 0, 0, 0.7);
    background-color: transparent;
    font-size: 20px;
  }
   
  #modalCloseBtn:hover {
    cursor: pointer;
  }

  .login-button {
    cursor: pointer;
  }